.post{
    min-height: 800px;
    width: 600px;
    border: 2px solid grey;
    border-radius: 1%;
    margin: 50px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    transition: 0.5s;
  }
  .post:hover{
    box-shadow: 0 -1px 30px 1px gray;
    transition: 0.5s;
  }
  .post-header{
      width: 100%;
      height: 80px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
  }
  .post-user{
      margin-left: 2vw;
  }
  .post-date{
      margin-right: 2vw;
  }
  .post-image{
      width: 550px;
      height: 550px;
      border: 1px solid grey;
      text-align: center;
      background-image: url(../imgs/default.jpg);
      background-position: center;
      background-size: cover;
  }
  .post-image:hover{
    cursor: pointer;
  }
  .post-footer{
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
  }
  .post-subtitle{
      margin-top: 10px;
      margin-bottom: 10px;
      width: 550px;
  }
  .post-description{
      width: 550px;
      margin-bottom: 30px;
  }
  @media(max-width:800px){
    .post{
        border: none;
        box-shadow: 0 -1px 10px 1px gray;
    }      
  }
  @media(min-width:451px) and (max-width:600px){
    .post{
        width: 400px;
        min-height: auto;
    }      
  }
  @media(max-width:600px){
    p{
        font-size: 10px;
    }
    .post{
        margin: 0;
        margin-bottom: 10px;
    }  
    .post-header{
        height: 40px;
    }
    .post-description{
        width: 350px;
    }
    .post-image{
        width: 350px;
        height: 350px;
    }
    .post-subtitle{
        width: 350px;
        margin-bottom: 0px;
    }
  }
  @media(max-width:450px){
    .post{
        width: 350px;
        min-height: auto;
    }
    .post-description{
        width: 300px;
        margin-bottom: 10px;
    }
    .post-subtitle{
        width: 300px;
        margin-top: 5px;

    }
  }
